.row{
 margin-left: 20px;
 color: white;
}

.row_posters{
display: flex;
padding: 20px;
overflow-y: hidden;
overflow-x: scroll;

}

.row_posters::-webkit-scrollbar{
 display: none;
}


.row_poster{
 width: 100%;
 max-height: 100px;
 margin-right: 10px;
 object-fit: contain;
 transition: transform 450ms;
}
.row_poster:hover{
 transform: scale(1.08);
}

.row_posterLarge{
 max-height: 250px;
}
.row_posterLarge:hover{
 transform: scale(1.09);
}